import React from 'react';
import '../page.css'
import '../section.css'
import './page9Sub.css'
import { Link, NavLink } from 'react-router-dom'
import MessageBar from './MessageBar'

class Page9Sub extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: []
        };

    }

    componentDidMount() {
        const settings = {
            method: 'GET',
            mode: 'no-cors',
        };
        var url = `https://haitang.twt.edu.cn/api/mg`;
        var ans = [];
        fetch(url, settings).then(function (response) {
            if (response.ok) {
                return response.json();
            }
        })
        .then((data) => {
            ans = data.data.message;
            this.setState({
                data: ans
            })
            // alert('');
        }
        ).catch(function (error) {

            alert(',');
        });

        // let ans = [
        //     {
        //         "nickname": "1",
        //         "message": "1."
        //     },
        //     {
        //         "nickname": "2",
        //         "message": "2."
        //     },
        //     {
        //         "nickname": "3",
        //         "message": "3."
        //     },
        //     {
        //         "nickname": "4",
        //         "message": "4."
        //     },
        //     {
        //         "nickname": "5",
        //         "message": "5."
        //     },
        //     {
        //         "nickname": "6",
        //         "message": "6."
        //     }
        // ]
        // this.setState({
        //     data: ans
        // })
    }



    render() {

        return (
            <div className="section">
                <div className="page">
                    <img className="page9Sub-background" src={require('./page9Sub_background.jpg')} alt=""></img>
                    <Link to="/#Page/9"><img className="page9Sub-back" src={require('./page9Sub_back.png')} alt=""></img></Link>
                    <MessageBar data={this.state.data}></MessageBar>
                </div>
            </div>
        );
    }

}

export default Page9Sub;